import * as React from 'react';
import Product from '../models/product';

interface ProductsBillInterface {
    total: number,
    prodsSolicitar: Product[]
}

function ProductsBill(props: ProductsBillInterface) {
    return (
        <React.Fragment>
            <h5 className="title is-5">Materiales Solicitados ({props.prodsSolicitar.length})</h5>
            <table className="table is-striped is-hoverable">
                <thead>
                    <tr>
                        <th>Item</th>
                        <th>Precio</th>
                        <th>Cantidad</th>
                        <th>Subtotal</th>
                    </tr>
                </thead>
                <tfoot>
                    <tr>
                        <th>Total</th>
                        <th></th>
                        <th></th>
                        <th>${props.total.toFixed(2)}</th>
                    </tr>
                </tfoot>
                <tbody>
                    {props.prodsSolicitar.map(p => {
                        return (
                            <tr key={p.codigo}>
                                <td>{p.comentarios}</td>
                                <td>${p.precioU}</td>
                                <td>{p.cantidad}</td>
                                <td>${parseFloat(p.precioT).toFixed(2)}</td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
        </React.Fragment>
    );
}

export { ProductsBill };
